const getters = {
	get_data_arr(state){
		return state.data_arr;
	}
}

const mutations = {
	add_data(data,idx){
		data.com_list=data.arr[idx];
	},
	add_car(data,str){
		data.car=str
	},
	add_set_data(state, data){
		var types = true;
		for(var i = 0; i < state.data_arr.length; i++){
			if(state.data_arr.text == data.text){
				state.data_arr.num++;
				types = false;
			}
		} 	
		if(types){
			state.data_arr.push(data)
		}
		console.log(state.data_arr)
	},
	//加
	set_jia(state, text){
		for(var i = 0; i < state.data_arr.length; i++){
			if(state.data_arr[i].text == text){
				state.data_arr[i].num++;
			}
		}
	},
	//减
	set_jian(state, text){
		for(var i = 0; i < state.data_arr.length; i++){
			if(state.data_arr[i].text == text){
				state.data_arr[i].num--;
				if(state.data_arr[i].num <= 1){
					state.data_arr[i].num = 1;
				}
			}
		}
	},
	//删除
	set_delete(state, text){
		for(var i = 0; i < state.data_arr.length; i++){
			if(state.data_arr[i].text == text){
				state.data_arr.splice(i,1)
			}
		}
	}

}
const actions = {
	add_com({commit},idx){
		commit('add_data', idx)
	},
	car_add({commit},str){
		commit('add_car',str)
	},
	set_data({commit}, data){
		commit('add_set_data',data)
	},
	//加
	set_jia({commit}, text){
		commit('set_jia',text)
	},
	//减
	set_jian({commit}, text){
		commit('set_jian',text)
	},
	//删除
	delete_arr({commit}, text){
		commit('set_delete',text)
	}
}

export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})


