import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/index.vue'
import Company from '../components/company.vue'
import Product from '../components/product.vue'
import News from '../components/news.vue'
import Recruit from '../components/recruit.vue'
import Tel from '../components/tel.vue'
import One from '../components/one.vue'
import Two from '../components/two.vue'
import Three from '../components/three.vue'
import Four from '../components/four.vue'
import Five from '../components/five.vue'
import Six from '../components/six.vue'
import Seven from '../components/seven.vue'
import Eight from '../components/eight.vue'
import Nine from '../components/nine.vue'
import Ten from '../components/ten.vue'
import Proone from '../components/proone.vue'
import Protwo from '../components/protwo.vue'
import Prothree from '../components/prothree.vue'
import Profour from '../components/profour.vue'
import Jqu from '../components/jqu.vue'
import Gw from '../components/gw.vue'
Vue.use(Router)

var router = new Router({
	routes: [{
			path: '/',
			name: 'index',
			component: Index,
			children: [{
				path: 'one',
				name: 'one',
				component: One,
			}, {
				path: 'two',
				name: 'two',
				component: Two,
			}, {
				path: 'three',
				name: 'three',
				component: Three,
			}, {
				path: 'four',
				name: 'four',
				component: Four,
			}, {
				path: '/',
				redirect: '/one'
			}]
		},
		{
			path: '/company',
			name: 'company',
			component: Company,
			children: [{
				path: 'five',
				name: 'five',
				component: Five,
			}, {
				path: 'six',
				name: 'six',
				component: Six,
			}, {
				path: 'seven',
				name: 'seven',
				component: Seven,
			}, {
				path: '*',
				redirect: '/five'
			}]
		},
		{
			path: '/product',
			name: 'product',
			component: Product,
			children: [{
				path: 'proone',
				name: 'proone',
				component: Proone,
			}, {
				path: '/proone',
				redirect: '/'
			}, {
				path: 'protwo',
				name: 'protwo',
				component: Protwo,
			}, {
				path: 'prothree',
				name: 'prothree',
				component: Prothree,
			}, {
				path: 'profour',
				name: 'profour',
				component: Profour,
			}]
		},
		{
			path: '/news',
			name: 'news',
			component: News,
			children: [{
				path: 'eight',
				name: 'eight',
				component: Eight,
			}, {
				path: '/eight',
				redirect: '/'
			}, {
				path: 'nine',
				name: 'nine',
				component: Nine,
			}, {
				path: 'ten',
				name: 'ten',
				component: Ten,
			}]
		},
		{
			path: '/recruit',
			name: 'recruit',
			component: Recruit
		},
		{
			path: '/tel',
			name: 'tel',
			component: Tel
		},
		{
			path: '/jqu',
			name: 'jqu',
			component: Jqu
		}, {
			path: '/',
			redirect: '*'
		},
		{
			path: '/gw',
			component: Gw
		}
	]
})
export default router


